@import "main-bx";

html {
    scroll-behavior: smooth;
    scroll-padding: 100px;
}

*,
*::before,
*::after {
    box-sizing: border-box;
    leading-trim: both;
    text-edge: cap;
}

h1, h2, h3, h4, h5, h6, ul, ol, p {
    margin: 0;
    padding: 0;
}

.container {
    width: 100%;
    max-width: 1720px;
    margin: 0 auto;
    padding: 0 40px;

    @include below(1440px) {
        max-width: 1330px;
    }

    @include below(1240px) {
        max-width: initial;
        padding: 0 25px;
    }

    @include below(1024px) {
        max-width: 768px;
        padding: 0 20px;
    }

    @include below(479px) {
        max-width: 100%;
        padding: 0 15px;
    }
}

body {
    font-family: $accent_font;
    font-size: 16px;
    line-height: 22px;
    width: 100%;
    // max-width: $body_width;
    margin: 0 auto;
    overflow-x: hidden;
   
    &.compensate-for-scrollbar {
        margin-right: auto !important;
    }

 

    // background-position: top left;
    // background-size: 100vw 35vh;
}

.main {
    
}

a {
    text-decoration: none;
}

input,
button,
textarea,
fieldset {
    background: transparent;
    border: 0;
    padding: 0;
}

button {
    cursor: pointer;
}

img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
    max-width: 100%;
}

ul {
    list-style-type: none;
}


:focus {
    outline-style: none;
    outline-width: 0 !important;
    outline-color: none !important;
}

.none {
    display: none;
}

.slick-slider {
    .slick-dots {
        bottom: -35px;
    }
    .slick-dots {
        li {
            background-color: #D9D9D9;
            border-radius: 50%;
            width: 6px;
            height: 6px;
            margin: 0 2.5px;
        }
    }

    .slick-dots button {
        display: none;
    }

    .slick-dots li {
        &.slick-active {
            background-color: #A9A9A9;
        }
    }
}