@import "main-bx";

.programs {
    padding-bottom: 107px;

    @include below(1440px) {
        padding-bottom: 116px;
    }

    @include below(1024px) {
        padding-bottom: 100px;
    }

    @include below(768px) {
        padding-bottom: 60px;
    }

    &.programs_second {
        padding: 50px 0 180px;

        @include below(1024px) {
            padding: 30px 0 120px;
        }

        @include below(768px) {
            padding: 0 0 60px;
        }
    }

    &__filter {

        .filter__flex{
            @include below(1240px) {
                flex-wrap: wrap;
                margin: -10px 0 0;
            }

            @include below(540px) {
                margin: -15px 0 0;
            }

            .filter__item {
                @include below(1240px) {
                    margin: 10px 45px 0 0;
                }

                @include below(540px) {
                    margin: 15px 0 0;
                }
            }
        }
    }

    &__head {
        display: flex;
        justify-content: space-between;
        align-items: end;
        margin-bottom: 57px;

        @include below(1440px) {
            margin-bottom: 48px;
        }

        @include below(768px) {
            margin-bottom: 32px;
        }
    }

    &__title {
        @include title;
        margin-bottom: 32px;

        @include below(1440px) {
            margin-bottom: 20px;
        }

        @include below(768px) {
            margin-bottom: 16px;
        }
    }

    &__text {
        @include text;
        color: $color_grey;
    }

    &__nav {

        @include below(1024px) {
            display: none;
        }


        &-btn {
            &:hover:not(:active) {
                .icon {
                    color: #a4a4a4;
                }
            }

            &:first-of-type {
                margin-right: 6px;
            }

            .icon {
                color: #EDEDED;
                width: 92px;
                height: 64px;
                transition: .2s linear;
                @include below(1440px) {
                    width: 70px;
                    height: 50px;
                }
            }
        }
    }

    &__row {
        display: flex;
        align-items: stretch;
        
        .slick-list {
            margin: 0 -9px;
            @include below(1440px) {
                // margin: 0;
            }
        }

        .slick-track {
            display: flex;
            align-items: stretch;
        }

        @include below(1440px) {
            .slick-track {
                // margin-left: -20%;
            }
        }

        @include below(1240px) {
            .slick-track {
                margin-left: 0%;
            }
        }

        @include below(1024px) {
            .slick-track {
                margin-left: -54%;
            }
        }

        @include below(600px) {
            .slick-track {
                margin-left: 0%;
            }
        }

        
    }

    &__item {
        width: calc(33.333% - 18px);
        margin: 0 9px;
        position: relative;
        cursor: pointer;
        overflow: hidden;
        transition: .3s linear;
        border-radius: 20px;

        @include below(768px) {
            border-radius: 10px;
        }

        &:not(:nth-child(-n+3)):not(.slick-slide) {
            display: none;
        }

        @include below(1440px) {
            &:not(:nth-child(-n+2)):not(.slick-slide) {
                display: none;
            }
        }

        @include below(1240px) {
            width: calc(50% - 18px);
        }

        @include below(1024px) {
            width: 100%;
            margin: 0 9px;

            &:not(:first-child):not(.slick-slide) {
                display: none;
            }
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border-radius: 20px;
            background-color: rgba(0, 0, 0, 0.35);
            transition: background-color .3s linear;

            @include below(768px) {
                border-radius: 10px;
            }
        }

        &:hover,
        &:focus {
            &::before {
                background-color: rgba(0, 0, 0, 0.5);
            }

            .programs__date {
                visibility: hidden;
                opacity: 0;
                display: none;
            }

            .programs__body-wrap {
                display: block;
            }
        }

        &-img {
            border-radius: 20px;

            
            @include below(768px) {
                border-radius: 10px;
            }

            img {
                border-radius: 20px;
  
                @include below(768px) {
                    border-radius: 10px;
                }
            }
        }
    }

    &__link {
        display: flex;
        width: 100%;
        max-width: 266px;
        margin: 77px auto 0;

        @include below(1440px) {
            max-width: 215px;
            margin: 54px auto 0;
        }
    
        @include below(540px) {
            max-width: initial;
        }
    }

    &__body {
        color: $color_white;
        position: absolute;
        left: 0;
        bottom: 0;
        padding: 0 50px 50px;

        @include below(1440px) {
            padding: 0 35px 35px;
        }

        @include below(1024px) {
            padding: 0 30px 30px;
        }

        @include below(768px) {
            padding: 0 15px 15px;
        }

        &-title {
            font-weight: 500;
            font-size: 32px;
            line-height: 43px;
            margin-bottom: 25px;

            @include below(1440px) {
                font-size: 25px;
                line-height: 120%;
                margin-bottom: 20px;
            }

            @include below(768px) {
                font-size: 20px;
                line-height: 1.2;
                margin-bottom: 15px;
            }

        }

        &-price {
            font-weight: 600;
            font-size: 25px;
            line-height: 34px;
            display: block;
            margin-bottom: 35px;

            @include below(1440px) {
                font-size: 20px;
                line-height: 120%;
                margin-bottom: 25px;
            }

            @include below(768px) {
                font-size: 20px;
                margin-bottom: 15px;
            }

            @include below(540px) {
                font-size: 18px;
                line-height: 22px;
            }
        }

        &-date {
            display: block;
            font-weight: 600;
            font-size: 18px;
            line-height: 25px;
            margin-bottom: 30px;

            @include below(1440px) {
                font-size: 14px;
                line-height: 120%;
                margin-bottom: 22px;
            }

            @include below(768px) {
                margin-bottom: 15px;
            }

            @include below(540px) {
                font-size: 16px;
                line-height: 22px;
            }
        }

        &-text {
            font-weight: 400;
            font-size: 18px;
            line-height: 28px;
            margin-bottom: 35px;

            @include below(1440px) {
                font-size: 14px;
                line-height: 120%;
                margin-bottom: 25px;
            }

            
            @include below(768px) {
                margin-bottom: 15px;
            }
        }

        &-wrap {
            display: none;
        }


        &-link {
            @include below(1440px) {
                height: 50px;
            }
            @include below(540px) {
                padding: 7px 0;
                width: 100%;
            }
        }

        &-flex {
            display: flex;
            justify-content: space-between;
            margin-bottom: 30px;

            .programs__body-date,
            .programs__body-price {
                margin-bottom: 0;
            }

            @include below(1024px) {
                margin-bottom: 15px;
            }
        }
    }

    &__date {
        font-weight: 600;
        font-size: 18px;
        line-height: 25px;
        transition: visibility .3s linear, opacity .3s linear, display .3s linear;

        @include below(540px) {
            font-size: 16px;
            line-height: 22px;
        }
    }

    &_second {

        
        .programs__row {
            display: flex;
            align-items: stretch;
            flex-wrap: wrap;
            margin: -40px -9px 0;

            @include below(768px) {
                margin: -20px 0 0;
            }

            .programs__item {
                display: block;   
                margin: 40px 9px 0;

                @include below(1440px) {
                    display: block;
                }
                
                @include below(1380px) {
                    width: calc(50% - 18px);
                    display: block;
                }

                @include below(1024px) {
                    display: block;
                }

                @include below(768px) {
                    width: 100%;
                    margin: 20px 0 0;
                }

                &::before {
                    background-color: rgba(0, 0, 0, 0.4);
                }

                &:hover {
                    .programs__body {
                        // bottom: 60px;
                    }

                    &::before {
                        background-color: rgba(25, 30, 36, 0.75);
                    }
                }

                .programs__body {

                    @include below(1024px) {
                        padding: 20px;
                    }

                    @include below(768px) {
                        // bottom: -100px;
                    }

                    &-title {
                        @include below(1024px) {
                            font-size: 24px;
                            line-height: 28px;
                        }
                    }
                }

                .programs__item-img {
                    @include below(768px) {
                        height: 500px;
                    }
                }
            }
        }
    }
}