@import "main-bx";

.tabs {

    &__list {
        display: flex;
        @include below(1024px) {
            overflow-y: hidden;
            overflow-x: auto;
        }
    }

    &__item {
        &:not(:last-child) {
            margin-right: 30px;

            @include below(1660px) {
                margin-right: 20px;
            }

            @include below(1024px) {
                margin-right: 5px;
            }
        }
    }

    &__button {
        font-weight: 600;
        font-size: 20px;
        color: #747676;
        line-height: 27px;
        text-align: center;
        border: 2px solid #EDEDED;
        border-radius: 100px;
        padding: 20px 28px;
        transition: background-color .3s linear, color .3s linear, border-color .3s linear;

        @include below(1660px) {
            font-size: 14px;
            line-height: 21px;
            padding: 12px 23px 15px 22px;
            white-space: nowrap;
        }


        &:hover,
        &:active {
            color: #000;
            background-color: #F0F2F4;
            border-color: #ededed;
        }

        &_active {
            color: #000;
            background-color: #F0F2F4;
            border-color: #ededed;
        }
    }

  
}
