@import "main-bx";

.hotel {

    padding-bottom: 170px;

    @include below(1440px) {
        padding-bottom: 64px;
    }

    @include below(1024px) {
        padding-bottom: 120px;
    }

    @include below(768px) {
        padding-bottom: 60px;
    }

    &.hotel_second {
        padding: 50px 0 180px;

        @include below(1024px) {
            padding: 30px 0 120px;
        }

        @include below(768px) {
            padding: 0 0 80px;
        }
    }

    &__head {
        margin-bottom: 60px;

        @include below(1440px) {
            margin-bottom: 35px;
        }

        @include below(768px) {
            margin-bottom: 32px;
        }
    }

    &__title {

        @include title;
        margin-bottom: 32px;

        @media (max-width: 1440px) {
            margin-bottom: 20px;
        }

        @include below(768px) {
            margin-bottom: 16px;
        }
    }

    &__text {
        @include text;
        color: $color_grey;
    }



    &__row {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin: -60px -9px 0;

        @include below(700px) {
            margin: -32px 0 0;
        }
    }

    &__item {
        width: calc(33.333% - 18px);
        margin: 64px 9px 0;
        cursor: pointer;
        display: flex;
        flex-direction: column;

        @include below(1440px) {
            margin: 60px 9px 0;
        }

        @include below(1100px) {
            width: calc(50% - 18px);
        }

        @include below(700px) {
            width: 100%;
            margin: 32px 0 0;
        }

        &:hover,
        &:focus {
            .hotel__info {
                visibility: visible;
                opacity: 1;
            }
        }
    }

    &__img {
        border-radius: 20px;
        margin-bottom: 32px;

        @include below(1440px) {
           margin-bottom: 26px;
        }

        @include below(768px) {
            border-radius: 10px;
        }

        img {
            border-radius: 20px;

            @include below(768px) {
                border-radius: 10px;
            }
        }
    }

    &__body {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
    }

    &__name {
        font-weight: 500;
        font-size: 32px;
        line-height: 43px;
        margin-bottom: 10px;

        @include below(1440px) {
            font-size: 25px;
            line-height: 120%;
        }

        // @include below(1024px) {
        //     font-size: 26px;
        //     line-height: 36px;
        // }

        @include below(768px) {
            font-size: 20px;
            line-height: 1.2;
        }
    }

    &__flex {
        display: flex;
        justify-content: space-between;
        font-weight: 500;
        font-size: 20px;
        line-height: 27px;
        color: $color_grey;
        margin-bottom: 40px;

        @include below(1440px) {
            margin-bottom: 22px;
        }
    }

    &__place {
        font-size: 18px;
        line-height: 24px;

        @include below(1440px) {
            font-size: 14px;
            line-height: 120%;
        }

        // @include below(1024px) {
        //     font-size: 18px;
        // }

        @include below(768px) {
            // font-size: 14px;
            // line-height: 1.6;
        }
    }

    &__price {
        font-weight: 500;
        font-size: 20px;
        line-height: 27px;
        margin-right: 30px;
        @include below(1440px) {
            font-size: 14px;
            line-height: 120%;
        }

        @include below(768px) {
            margin-right: 0;
        }
    }

    &__info {
        display: flex;
        justify-content: space-between;
        align-items: center;
        opacity: 0;
        margin-top: auto;
        visibility: hidden;
        transition: opacity .3s linear, visibility .3s linear;


        &-link {
            font-weight: 600;
            font-size: 20px;
            line-height: 27px;
            color: $color_black;
            position: relative;
            padding-bottom: 9px;
            @include below(1440px) {
                font-size: 14px;
                line-height: 120%;
                margin-right: 26px;
            }
            @include below(1240px) {
                margin-right: 0;
            }

            &::after {
                content: "";
                width: 100%;
                height: 2px;
                position: absolute;
                left: 0;
                bottom: 0;
                background-color: $color_black;
            }

            @include below(768px) {

                &:hover:not(:active) {
                    color: $color_white;
                    background-color: $color_black;
                }
            }
        }

        @include below(1420px) {
            visibility: visible;
            opacity: 1;
        }

        @include below(1240px) {
            align-items: start;
        }
        
        &_second {
            visibility: visible;
            opacity: 1;
        }

        &-btn {
            @include below(1024px) {
                padding: 7px 15px;
            }
        }
    }

    
    &__link {
        display: flex;
        width: 100%;
        max-width: 266px;
        padding: 7px;
        margin: 70px auto 0;

        @include below(1440px) {
            max-width: 240px;
            margin: 44px auto 0;
        }

        @include below(540px) {
            max-width: initial;
        }
    }
}