@import "main-bx";

.about {
    padding: 150px 0 70px;

    @include below(1440px) {
        padding: 122px 0 55px;
    }

    // @include below(1240px) {
    //     padding: 20px 0 70px;
    // }

    @include below(768px) {
        padding: 60px 0;
    }

    &__head {
       margin-bottom: 32px;
        @include below(1440px) {
            margin-bottom: 18px;
        }
        @include below(1440px) {
            margin-bottom: px;
        }
        &-row {
            display: flex;
            align-items: center;
            font-weight: 600;
            font-size: 18px;
            line-height: 50px;
            color: $color_black;

            @include below(768px) {
                font-size: 14px;
                line-height: 1.6;
            }
        }

        &-place {
            margin-right: auto;
            color: $color_black;
            display: flex;
            align-items: center;

            img {
                width: 20px;
                height: 24px;
                margin-right: 10px;

                @include below(1440px) {
                    width: 14px;
                    height: 18px;
                }
            }

            @include below(1440px) {
                font-size: 14px;
            }

            @include below(768px) {
            }
        }

        &-btn {
            height: 50px;
            display: flex;
            align-items: center;
            border-radius: 50px;
            background-color: #F0F2F4;
            border: 1px solid #ededed;
            padding: 7px 26px;
            transition: background-color .3s linear, border-color .3s linear, color .3s linear;
        
            @include below(1440px) {
                font-size: 14px;
                padding: 5px 20px;
                height: 40px;
            }

            @include below(600px) {
                padding: 5px 13px;
            }

            &:hover {
                background-color: $color_black;
                color: $color_white;
                border-color: $color_black;
            }
            &:first-of-type{
                margin-right: 10px;
            }

            span {
                @include below(600px) {
                    display: none;
                }
            }

            .icon {
                width: 18px;
                height: 18px;
                margin-right: 11px;

                @include below(1440px) {
                    width: 14px;
                    height: 14px;
                    margin-right: 5px;
                }
                @include below(600px) {
                    margin-right: 0;
                }
            }
        }
    }

    &__title {
        @include title;
        margin-bottom: 28px;

        @include below(1440px) {
            margin-bottom: 6px;
        }

        @include below(768px) {
            margin-bottom: 16px;
        }
    }

    &__wrap {
        display: flex;
        margin: 0 -10px;

        @include below(1024px) {
            flex-wrap: wrap;
            margin: 0;
        }
    }

    &__col {
        width: calc(50% - 20px);
        margin: 0 10px;

        
        @include below(1024px) {
            width: 100%;
            margin: 12px 0 0;
        }

        &:last-of-type {
            display: flex;
            flex-wrap: wrap;
            align-items: flex-start;
            @include below(1024px) {
                flex-wrap: nowrap;
            }

            .about__item {
                @include below(1024px) {
                    height: 250px;

                    .about__img {
                        height: 100%;
                    }
                }
                @include below(600px) {
                    height: 108px;
                }
                &:nth-child(1) {
                    width: 100%;
                    margin-bottom: 20px;
    
                    @include below(1440px) {
                        margin-bottom: 14px;
                    }
    
                    @include below(1024px) {
                        width: 33.33333%;
                        margin-bottom: 0;
                        margin-right: 12px;
                    }
                }
    
                &:nth-child(2) {
                    margin-right: 2%;
                    @include below(1024px) {
                        margin-right: 12px;
                    }
                }
    
                &:nth-child(2),
                &:nth-child(3) {
                    width: 49%;
    
                    @include below(1024px) {
                        width: 33.33333%;
                    }
                }
            }
        }
    }

    &__item {
        border-radius: 20px;
        position: relative;
        @include below(768px) {
            border-radius: 10px;
        }

        &::before {
            content: "";
            height: 100%;
            width: 100%;
            position: absolute;
            top: 0;
            left: 0;
            right: 0;
            background-color: rgba(0, 0, 0, 0.4);
            border-radius: 20px;
            opacity: 0;
            transition: opacity .3s linear;

            @include below(768px) {
                border-radius: 10px;
            }
        }

        &-content {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            color: $color_white;
            opacity: 0;
            transition: opacity .3s linear;

            
        }

        &:hover,
        &:focus {
            &::before {
                opacity: 1;
            }

            .about__item-content {
                opacity: 1;
            }
        }

        &-text {
            font-weight: 600;
            font-size: 20px;
            line-height: 28px;
            position: relative;
            padding-bottom: 10px;
            text-align: center;
            display: block;
            &::after {
                content: "";
                width: 100%;
                height: 2px;
                background-color: #fff;
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
            }

            
            @include below(1440px) {
                font-size: 14px;
                line-height: 20px;
            }

            @include below(1024px) {
                text-transform: capitalize;
                span {
                    display: none;
                }
            }
        }
    }

    &__img {
        @include below(1024px) {
            height: 335px;
            display: block;
        }

        @include below(600px) {
            height: 239px;
        }

        img {
            border-radius: 20px;
            height: 100%;
            display: block;
            @include below(768px) {
                border-radius: 10px;
            }
        }
    }

    &__inner {
        margin-top: 37px;
        display: flex;
        align-items: start;
        justify-content: space-between;

        @include below(1440px) {
            margin-top: 33px;
        }

        @include below(1024px) {
            margin-top: 32px;
            flex-direction: column;
        }
    }

    &__main {
        width: 100%;
        max-width: 950px;
        margin-right: auto;

        @include below(1590px) {
            max-width: 720px;
        }

        @include below(1024px) {
            max-width: initial;
        }
    }

    &__tabs {
        // .tabs__list {
        //     @include below(1024px) {
        //         flex-wrap: wrap;
        //         margin: -10px 0 0;
        //     }

        //     @include below(575px) {
        //         flex-direction: column;
        //     }
        // }

        // .tabs__item {
        //     @include below(1024px) {
        //         margin: 10px 24px 0 0;
        //     }

        //     @include below(575px) {
        //         margin: 10px 0 0;
        //     }
        // }

        // .tabs__button {
        //     @include below(575px) {
        //         width: 100%;
        //     }
        // }
    }

    &__description {
        margin: 48px 0;

        @include below(1440px) {
            margin: 35px 0 28px;
        }

        @include below(768px) {
            margin: 24px 0;
        }

        &-title {
            font-weight: 600;
            font-size: 32px;
            line-height: 43px;
            margin-bottom: 23px;

            @include below(1440px) {
                font-size: 25px;
                line-height: 34px;
                margin-bottom: 16px;
            }

            @include below(1024px) {
                font-style: 20px;
                line-height: 28px;
            }
        }

        &-text {
            font-weight: 500;
            font-size: 22px;
            line-height: 33px;
            color: $color_grey;

            @include below(1440px) {
                font-size: 16px;
                line-height: 26px;
            }

            p {
                &:first-child {
                    margin-bottom: 33px;
                    @include below(1440px) {
                        margin-bottom: 26px;
                    }
                }
            }
        }
    }

    &__services {

        &-flex {
            display: flex;
            @include below(1024px) {
                flex-direction: column;
            }
        }

        &-col {
            &:not(:first-child) {
                margin-left: 96px;

                @include below(1024px) {
                    margin: 30px 0 0;
                }

                @include below(768px) {
                    margin: 15px 0 0;
                }
            }
        }

        &-title {
            font-weight: 600;
            font-size: 32px;
            line-height: 43px;
            margin-bottom: 23px;

            @include below(1440px) {
                font-size: 25px;
                line-height: 34px;
                margin-bottom: 16px;
            }
        }

        &-img {
            flex-shrink: 0;
            img {
                
                width: 52px;
                height: 52px;

                @include below(1440px) {
                    width: 38px;
                    height: 38px;
                }
            }
        }

        &-item {
            white-space: nowrap;
            display: flex;
            align-items: center;

            &:not(:first-child) {
                margin-top: 30px;

                @include below(1440px) {
                    margin-top: 20px;
                }
            }

            span {
                margin-left: 16px;
                font-weight: 500;
                font-size: 23px;
                line-height: 31px;
                color: $color_grey;

                @include below(1440px) {
                    font-size: 16px;
                    line-height: 21px;
                }
            }

            button {
                font-weight: 500;
                font-size: 23px;
                line-height: 31px;
                padding: 10px 15px;
                border-radius: 50px;
                border: 2px solid #EDEDED;

                @include below(1440px) {
                    font-size: 16px;
                    line-height: 21px;
                    padding: 8px 12px;
                }
            }
        }
    }

    &__right {
        width: 100%;
        max-width: 596px;
        padding: 40px 32px;
        border: 1px solid #EDEDED;
        border-radius: 20px;
        margin-left: 32px;
        @include below(1440px) {
            max-width: 452px;
            padding: 30px 24px;
        }

        @include below(1024px) {
            max-width: initial;
            padding: 30px;
            margin-left: 0;
            margin-top: 54px;
        }

        @include below(768px) {
            padding: 15px;
        }

        &-title {
            font-weight: 600;
            font-size: 23px;
            line-height: 31px;
            margin-bottom: 30px;

            @include below(1440px) {
                font-size: 16px;
                line-height: 22px;
                margin-bottom: 25px;
            }

            @include below(1024px) {
                font-size: 23px;
                line-height: 31px;
                margin-bottom: 20px;
            }
        }

        &-item {
            display: flex;

            &_first {
                margin-bottom: 20px;
                @include below(1440px) {
                    margin-bottom: 12px;
                }
            }
            
            .icon {
                flex-shrink: 0;
                width: 24px;
                height: 22px;
                margin-right: 20px;

                @include below(1440px) {
                    width: 18px;
                    height: 16px;
                    margin-right: 15px;
                }
            }

            p {
                font-weight: 500;
                font-size: 18px;
                line-height: 28px;
                color: $color_grey;
                span {
                    display: block;
                    color: $color_black
                }

                @include below(1440px) {
                    font-size: 14px;
                    line-height: 21px;
                }
            }
        }
    }
}