@import "main-bx";

.breadcrumbs {
    padding: 40px 0;

    @include below(1440px) {
        padding: 32px 0;
    }

    @include below(768px) {
        padding: 28px 0;
    }

    &__list {
        display: flex;
        flex-wrap: wrap;
        // @include below(575px) {
        //     overflow: hidden;
        //     overflow-x: auto;
        // }
    }

    &__item {
        font-weight: 600;
        font-size: 15px;
        line-height: 20px;

        // white-space: nowrap;

        @include below(1440px) {
            font-size: 12px;
            line-height: 16.39px;
        }

        &:not(:last-child) {
            &:after {
                content: ">";
                display: inline-block;
                color: $color_black;
                margin: 0 5px;
            }
        }

        a {
            color: $color_black;
        }

        span {
            color: $color_grey;
        }
    }

    &_absolute {
        position: absolute;
        z-index: 100;
        width: 100%;


        @include below(1240px) {
            position: static;
        }

        .breadcrumbs__list {
            .breadcrumbs__item {
                white-space: nowrap;
                &:after {
                    color: $color_white;
                }

                a {
                    color: $color_white;
                }

                span {
                    color: #CFCFCF;
                }

                @include below(1240px) {
                    &:after {
                        color: $color_black;
                    }

                    a {
                        color: $color_black;
                    }
            
                    span {
                        color: $color_grey;
                    }
                }
            }
        }
    }
}