@mixin h1() {
    margin-top: 20px;
    margin-bottom: 20px;
}

@mixin font-face($name, $path, $weight, $style) {
    @font-face {
        font-family: $name;
        src: url($path + ".woff2") format("woff2"),
        url($path + ".woff") format("woff");
        font-weight: $weight;
        font-style: $style;
        font-display: swap;
    }
}

@mixin below($width) {
    @media (max-width: $width) {
        @content;
    }
}

@mixin above($width) {
    @media (min-width: $width) {
        @content;
    }
}

@mixin below_and_above($max,$min) {
    @media (max-width: $max) and (min-width: $min) {
        @content;
    }
}

@mixin title {
    font-size: 50px;
    font-weight: 600;
    line-height: 60px;

    @include below(1440px) {
        font-size: 35px;
        line-height: 45px;
    }

    @include below(768px) {
        font-size: 28px;
        line-height: 35px;
    }

    span {
        font-weight: 300;
    }

    
}


@mixin text {
    font-weight: 500;
    font-size: 22px;
    line-height: 33px;

    @include below(1440px) {
        font-size: 16px;
        line-height: 26px;
    }
    
    @include below(1024px) {
        font-size: 20px;
        line-height: 28px;
    }

    @include below(768px) {
        font-size: 16px;
        line-height: 28px;

        br {
            display: none;
        }
    }
}