@import "main-bx";

.form {

    &__wrap {
        display: flex;
        flex-direction: column;
        width: 100%;
    }

    &__label {
        display: block;

        &:not(:last-child) {
            margin-bottom: 10px;
        }
    }

    &__input {
        width: 100%;
        padding: 18px 0;
        border-bottom: 2px solid transparent;
        font-weight: 600;
        font-size: 20px;
        line-height: 27px;

        @include below(1440px) {
            font-size: 14px;
            line-height: 120%;
            padding: 13px 0;
        }

        // @include below(768px) {
        //     padding: 15px 0;
        // }
    }

    &__btn {
        
    }
}