@import "main-bx";

.btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    height: 64px;
    padding: 7px 34px;
    color: $color_white;
    font-size: 20px;
    font-weight: 600;
    line-height: 50px;
    border: 1px solid $color_white;
    border-radius: 50px;
    transition: color .3s linear, background-color .3s linear;

    &:hover:not(:active) {
        color: $color_black;
        background-color: $color_white;
    }

    &_black {
        color: $color_black;
        border: 1px solid $color_black;

        &:hover:not(:active) {
            color: $color_white;
            background-color: $color_black;
        }
    }

    @include below(1440px) {
        font-size: 14px;
        height: 50px;
    }

    svg {
        width: 31px;
        height: 9px;
        margin-left: 20px;
        @include below(1440px) {
            width: 24px;
            height: 7px;
        }
    }

    @include below(768px) {
        height: 40px;
        font-size: 14px;
        line-height: 1.6;
    }

 

}