@import "main-bx";

.number {

    padding: 82.5px 0 190px;

    @include below(1440px) {
        padding: 55px 0 112px;
    }

    @include below(1240px) {
        padding: 50px 0 100px;
    }

    @include below(768px) {
        padding: 0 0 60px;
    }

    &__title {
        @include title;   
        margin-bottom: 32px; 

        @include below(1440px) {
            margin-bottom: 22px;
        }

        @include below(768px) {
            margin-bottom: 16px;
        }
    }

    &__tabs {
        margin-bottom: 40px;

        @include below(1440px) {
            margin-bottom: 30px;
        }
    }

    &__row {
        display: flex;
        align-items: stretch;
        flex-wrap: wrap;
        margin: -70px -10px 0;

        @include below(1024px) {
            width: 100%;
            margin: -32px 0 0;
        }
    }

    &__card{
        width: calc(50% - 20px);
        margin: 72px 10px 0;
        display: flex;
        flex-direction: column;

        @include below(1440px) {
            margin: 68px 10px 0;
        }

        @include below(1024px) {
            width: 100%;
            margin: 32px 0 0;
        }
    }

    &__img {
        border-radius: 20px;
        @include below(768px) {
            border-radius: 10px;
        }

        img {
            border-radius: 20px;

            &:not(:first-child):not(.slick-slide) {
                display: none;
            }

            @include below(768px) {
                border-radius: 10px;
            }
        }
    }

    &__nav {
        padding: 0 20px;
        margin: 20px 0;
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include below(1440px) {
            padding: 0 13px;
            margin: 13px 0;
        }

        @include below(768px) {
            padding: 0;
            margin: 12px 0;
        }

        &-buttons {
            button {
                &:hover:not(:active) {
                    .icon {
                        color: #a4a4a4;
                    }
                }

                .icon {
                    width: 92px;
                    height: 64px;
                    color: #EDEDED;
                    transition: .2s linear;
                    @include below(1440px) {
                        width: 70px;
                        height: 50px;
                    }

                    @include below(768px) {
                        width: 60px;
                        height: 42px;
                    }
                }
            }
        }
    }

    &__counter {
        font-weight: 300;
        font-size: 22px;
        line-height: 33px;

        @include below(1440px) {
            font-size: 16px;
        }
    }

    &__body {
        flex-grow: 1;
        display: flex;
        flex-direction: column;
        align-items: start;

        &-title {
            font-weight: 600;
            font-size: 32px;
            line-height: 43px;
            color: #000;
            margin-bottom: 22px;

            @include below(1440px) {
                font-size: 25px;
                line-height: 32px;
                margin-bottom: 14px;
            }

            @include below(768px) {
                font-size: 20px;
                line-height: 28px;
            }
        }
    }

    &__flex {
        display: flex;
        margin: 0 0 45px;

        @include below(1440px) {
            margin: 0 0 26px;
        }

        @include below(1240px) {
            margin: 0 0 24px;
        }

        @include below(600px) {
            flex-direction: column;
        }
    }

    &__col {
        margin: 10px 0 0;
        @include below(600px) {
            margin: 0;
        }
        &:nth-child(2) {
            margin-left: 120px;
            @include below(1440px) {
                margin-left: 80px;
            }

            @include below(1280px) {
                margin-left: 32px;
            }
            @include below(600px) {
                margin-left: 0;
            }
        }

        &:nth-child(3) {
            margin-left: 60px;
            @include below(1440px) {
                margin-left: 40px;
            }
            @include below(1280px) {
                margin-left: 32px;
            }
            @include below(600px) {
                margin-left: 0;
            }
        }

        &-item {
            display: flex;
            align-items: center;
            font-weight: 500;
            font-size: 22px;
            line-height: 33px;
            color: $color_grey;

            @include below(1660px) {
                white-space: nowrap;
                font-size: 16px;
            }

            @include below(1024px) {
                white-space: initial;
            }

            .icon {
                flex-shrink: 0;
                width: 17px;
                height: 17px;
                margin-right: 15px;
                @include below(1660px) {
                    width: 12px;
                    height: 12px;
                    margin-right: 12px;
                }

                @include below(768px) {
                    width: 15px;
                    height: 15px;
                    margin-right: 15px;
                }
            }

            &:first-of-type {
                margin-bottom: 10px;
                @include below(1440px) {
                    margin-bottom: 0;
                }
            }


            span sup {
                font-size: 12px;
                @include below(1660px) {
                    font-size: 10px;
                }
            }
        }
    }

    &__button {
        margin-top: auto;

        @include below(768px) {
            width: 100%;
            max-width: 200px;
            padding: 7px 20px;
        }
    }
}