@import "main-bx";

.header {
    padding: 26px 0;

    @include below(1440px) {
        padding: 18px 0;
    }

    @include below(768px) {
        padding: 10px 0;
    }

    &_main {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        z-index: 100;
    }

    &_second {
        border-bottom: 1px solid #EDEDED;

        .header__logo {
            color: $color_black;
        }
        .header__list {

            &-link {
                color: $color_black;

                &:after {
                    background-color: $color_black;
                }
            }
        }

        .header__burger-line1,
        .header__burger-line2,
        .header__burger-line3 {
            background-color: $color_black;
        }

        .header__menu {
            background-color: $color_white;
            // box-shadow: 0px 10px 20px 10px #00000026;
        }

    }

    &__logo {
        color: $color_white;
        position: relative;
        z-index: 100;

        .icon {
            width: 240px;
            height: 58px;

            @include below(1440px) {
                width: 179px;
                height: 35px;
            }

            @include below(992px) {
                width: 240px;
                height: 48px;
            }

            @include below(768px) {
                width: 174px;
                height: 30px;
            }
        }

        .header.is-show & {

            color: #000;
            position: relative;
            z-index: 300;

        }
    }

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__nav {
        @include below(992px) {
            display: none;
        }
    }

    &__list {
        display: flex;

        &-item { 
            &:not(:last-child) {
                margin-right: 42px;
                @include below(1440px) {
                    margin-right: 35px;
                }
            }
        }

        &-link {
            color: $color_white;
            font-weight: 600;
            font-size: 18px;
            line-height: 15px;
            padding-bottom: 10px;
            position: relative;

            @include below(1440px) {
                font-size: 14px;
            }

            &::after {
                content: "";
                width: 100%;
                height: 2px;
                background-color: $color_white;
                position: absolute;
                left: 0;
                bottom: 0;
                opacity: 0;
                transition: opacity .3s linear;
            }

            &:hover {
                &:after {
                    opacity: 1;
                }
            }

            &_active {
                &:after {
                    opacity: 1;
                }
            }

        }
    }

    &__burger {
        display: none;
        padding: 5px;

        @include below(992px) {
            display: block;
            position: relative;
            z-index: 100;
        }

        &.is-change {
            .header__burger-line1 {
                transform: rotate(-45deg) translate(-4px, 2px);
                background-color: $color_black;
            }

            .header__burger-line2 {
                opacity: 0;
            }

            .header__burger-line3 {
                transform:  rotate(45deg) translate(-6px, -5px);
                background-color: $color_black;
            }
        }
    }


    &__burger-line1,
    &__burger-line2,
    &__burger-line3 {
        display: block;
        width: 25px;
        height: 2px;
        background-color: $color_white;
        border-radius: 1px;
        transition: transform .3s linear, opacity .3s linear;
    }

    &__burger-line1 {
        margin-bottom: 4px;
    }

    &__burger-line2 {
        margin-bottom: 4px;
    }

    &__social {
        display: flex;
        align-items: center;
        margin-top: 64px;

        a  {
            width: 42px;
            height: 42px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin: 0 5px;
        }

        a img {
            width: 28px;
            height: 28px;
        }
    }

    &__btn {
        background: #000;
        margin: auto 0 50px;
    }

    &__menu {
        position: fixed;
        top: 0;
        right: 0;
        padding: 100px 40px 0;
        width: 100%;
        max-width: 500px;
        height: 100%;
        background-color: rgba($color_white, 1);
        transform: translateX(100%);
        visibility: hidden;
        overflow-y: auto;
        z-index: 99;
        display: none;
        transition: transform .3s linear, visibility .3s linear, display .3s linear;


        @include below(992px) {
            display: flex;
            flex-direction: column;
            align-items: center;
        }

        @include below(768px) {
            max-width: 100%;
            padding: 160px 20px 0;
        }

        @include below(479px) {

        }

        &.is-show {
            visibility: visible;
            transform: translateX(0);
        }


        .header__nav {
            display: block;
        }

        .header__list {
            flex-direction: column;
            @include below(768px) {
                align-items: center;
            }
            &-item{
                &:not(:last-child) {
                    margin: 0 0 32px;
                    @include below(768px) {
                        margin: 0 0 20px;
                    }
                }
            }

            &-link {
                color: $color_black;
                font-size: 24px;
                line-height: 1.4;
                &:after {
                    background-color: $color_black;
                }
            }
        }
    }

}