@import "main-bx";


.services {
    padding: 165px 0;

    @include below(1440px) {
        padding: 115px 0;
    }

    @include below(1240px) {
        padding: 60px 0;
    }

    &__head {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 145px;

        @include below(1440px) {
            margin-bottom: 110px;
        }

        @include below(1380px) {
            margin-bottom: 100px;
        }

        @include below(1280px) {
            flex-direction: column;
            align-items: start;
            margin-bottom: 65px;
        }

        @include below(768px) {
            margin-bottom: 32px;
        }
    }

    &__title {
        @include title;
        max-width: 620px;
        @include below(1440px) {
            max-width: 400px;
        }
        @include below(1280px)  {
            margin-bottom: 32px;
        }

        @include below(768px) {
            margin-bottom: 16px;
        }
    }

    &__text {
        @include text;
        max-width: 648px;
        margin-top: -13px;
        color: $color_grey;

        @include below(1440px) {
            max-width: 490px;
            margin-top: 0;
        }

        @include below(1280px) {
            max-width: initial;
        }
    }

    &__row {
        display: flex;

        @include below(1550px) {
            margin: 0 -10px;
        }

        @include below(1240px) {
            flex-wrap: wrap;
            margin: -20px 0 0;
        }
    }

    &__left {
        width: 100%;
        max-width: 670px;
        margin-right: 20px;

        @include below(1550px) {
            width: calc(50% - 20px);
            max-width: initial;
            margin: 0 10px;
        }

        @include below(1440px) {
            width: auto;
            max-width: 510px;
        }

        @include below(1240px) {
            width: 100%;
            max-width: initial;
            margin: 20px 0 0;
        }
    }

    &__right {
        width: 100%;
        max-width: 950px;
        display: flex;
        flex-wrap: wrap;

        
        @include below(1550px) {
            width: calc(50% - 20px);
            max-width: initial;
            margin: 0 10px;
        }

        @include below(1440px) {
            width: auto;
            max-width: 720px;
        }

        @include below(1240px) {
            width: 100%;
            margin: 20px 0 0;
            max-width: initial;
        }

        .services__item {
            &:nth-child(1) {
                margin-right: 2%;

                @include below(768px) {
                    margin: 0 0 20px;
                }
            }

            &:nth-child(1),
            &:nth-child(2) {
               width: 49%;

                @include below(768px) {
                    width: 100%;
                }
            }

            &:nth-child(3) {
                width: 100%;
                margin: 20px 0 0;
            }
        }
    }

    &__item {
        display: block;
        overflow: hidden;
        position: relative;
        color: $color_white;
        border-radius: 20px;
        transition: .3s linear;

        @include below(768px) {
            border-radius: 10px;
        }

        &-img {
            border-radius: 20px;

            @include below(768px) {
                border-radius: 10px;
            }

            @include below(1240px) {
                height: 450px;
                display: block;
            }

            @include below(600px) {
                height: 480px;
            }

            img {
                border-radius: 20px;

                // @include below(1240px) {
                //     height: content;
                // }

                @include below(768px) {
                    border-radius: 10px;
                }
            }
        }

        &::before {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            border-radius: 20px;
            background-color: rgba(0, 0, 0, 0.3);
            transition: background-color .3s linear;

            @include below(768px) {
                border-radius: 10px;
            }
        }

        &:hover,
        &:focus {
            &::before {
                background-color: rgba(0, 0, 0, 0.5);
            }

            .services__item-text {
                display: block;
            }
        }

        &-content {
            position: absolute;
            left: 0;
            bottom: 0;
            padding: 0 40px 40px;
            transition: bottom .3s linear;

            @include below(1440px) {
                padding: 0 30px 30px;
            }

            @include below(768px) {
                padding: 0 15px 15px;
            }
        }

        &-title {
            display: block;
            font-weight: 500;
            font-size: 32px;
            line-height: 60px;

            @include below(1550px) {
                font-size: 28px;
                line-height: 44px;
            }

            @include below(1440px) {
                font-size: 25px;
            }

            @include below(768px) {
                font-size: 20px;
                line-height: 1.2;
                margin-bottom: 15px;
            }
        }

        &-text {
            display: block;
            font-weight: 500;
            font-size: 18px;
            line-height: 28px;
            transition: opacity .3s linear, visibility .3s linear;
            display: none;

            @include below(1440px) {
                font-size: 14px;
                line-height: 22px;
            }

            @include below(768px) {
                font-size: 14px;
                line-height: 1.6;
            }
        }
    }
}