@import "main-bx";

.intro {
    
    position: relative;

    &.intro_second {
        .intro__img {
            height: 828px;

            @include below(1440px) {
                height: 620px;
            }

            @include below(768px) {
                height: 500px;
            }
        }
    }
    &__img {
        height: 942px;

        @include below(1440px) {
            height: 704px;
        }

        @include below(768px) {
            height: 700px;
        }
    }

    &__content {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        padding: 360px 0 0;
        width: 100%;
        margin: 0 auto;
        color: $color_white;

        @include below(1900px) {
            padding: 240px 0 0;
        }

        @include below(1500px) {
            padding: 190px 0 0;
        }

        @include below(1440px) {
            padding: 256px 0 0;
        }

        @include below(1240px) {
            padding: 225px 0 0;
        }
    }

    &__title {
        font-family: $kudryashev_font;
        font-size: 100px;
        line-height: 110px;
        text-align: center;
        margin-bottom: 196px;

        @include below(1900px) {
            font-size: 80px;
            line-height: 90px;
            margin-bottom: 120px;
        }

        @include below(1440px) {
            font-size: 75px;
            line-height: 85px;
            margin-bottom: 145px;
        }    

        @include below(1240px) {
            font-size: 43px;
            line-height: 47px;
            margin-bottom: 94px;
        }
    }

    &__row {
        display: flex;
        justify-content: space-between;
        align-items: center;

        @include below(1240px) {
            flex-direction: column;
        }
    }


    &__text {
        font-weight: 600;
        font-size: 20px;
        line-height: 31px;

        @include below(1440px) {
            font-size: 16px;
            line-height: 24px;
        }

        @include below(1240px) {
            text-align: center;
            margin-bottom: 32px;
        }
    }

    &__btn {
        width: 100%;
        max-width: 306px;

        @include below(1440px) {
            max-width: 240px;
        }

        &_second {
            max-width: 360px;
        }

        // @include below(1240px) {
        //     margin-top: 30px;
        //     border-color: $color_black;
        //     color: $color_black;

        //     &:hover:not(:active) {
        //         color: $color_white;
        //         background-color: $color_black;
        //     }
        // }

        @include below(540px) {
            width: 100%;
        }

        svg {
            @include below(1440px) {
                width: 22px;
            }
        }
    }

    &.intro_second {
        .intro__content {
            padding: 268px 40px 0;

            @include below(1800px) {
                padding: 180px 40px 0;
            }

            @include below(1440px) {
                padding: 212px 40px 0;
            }

            @include below(1380px) {
                padding: 120px 40px 0;
            }

            @include below(1240px) {
                padding: 60px 0 100px;
            }

            @include below(768px) {
                padding: 75px 0;
            }
        }

        .intro__title {
            margin-bottom: 170px;
            @include below(1440px) {
                margin-bottom: 116px;
            }
            @include below(768px) {
                margin-bottom: 57px;
            }
        }

        .intro__text {
            @include below(1440px) {
                font-size: 14px;
            }
            @include below(768px) {
                margin-bottom: 32px;
            }
        }
        .intro__btn {
            max-width: 357px;

            @include below(1440px) {
                max-width: 270px;
            }
        }
    }

}