@import "main-bx";


.footer {
    background-color: #191E24;
    padding: 184px 0 50px;
    color: $color_white;
    margin-bottom: -40px;

    @include below(1500px) {
        padding: 100px 0 64px;
    }

    @include below(1440px) {
        padding: 100px 0 24px;
    }

    @include below(768px) {
        padding: 64px 0;
    }

    &__row {
        display: flex;
        justify-content: space-between;
        margin-bottom: 186px;

        @include below(1500px) {
            flex-direction: column;
            margin-bottom: 80px;
        }

        @include below(1440px) {
            flex-direction: row;
            margin-bottom: 136px;
        }
        
        @include below(1024px) {
            flex-direction: column;
        }

    }

    &__info {

        @include below(1500px) {
            margin-bottom: 40px;
        }
    }

    &__logo {
        display: block;
        margin-top: -25px;
        margin-bottom: 22px;
        color: $color_white;

        @include below(1500px) {
            margin-top: 0;
        }

        .icon {
            width: 240px;
            height: 58px;

            @include below(1440px) {
                width: 178px;
                height: 49px;
            }

            @include below(768px) {
                width: 174px;
                height: 30px;
            }
        }
    }

    &__info-text {
        font-size: 20px;
        font-weight: 400;
        line-height: 31px;
        margin-bottom: 52px;

        @include below(1440px) {
            font-size: 14px;
            line-height: 120%;
        }

        
        @include below(768px) {
            margin-bottom: 46px;
        }

        // @include below(768px) {
        //     font-size: 14px;
        //     line-height: 1.6;
        // }
    }

    &__social {
        display: flex;

        &-link {
            
            &:not(:last-child) {
                margin-right: 12px;
            }

            .icon {
                width: 42px;
                height: 42px;

                @include below(1440px) {
                    width: 32px;
                    height: 32px;
                }
            }
        }
    }

    &__menu {
        display: flex;

        @include below(1500px) {
           margin: 0 -10px;
        }

        @include below(1440px) {
            padding-top: 32px;
            max-width: 610px;
            flex: auto;
            margin: 0;
        }

        @include below(1024px) {
            flex-wrap: wrap;
            margin: -20px -10px 0;
        }

        @include below(768px) {
            flex-direction: column;
            margin: 0;
            padding-top: 0;
        }
    }

    &__col {

        &:not(:last-child) {
            margin-right: 132px;
            @include below(1440px) {
                margin-right: 0;  
            }

            @include below(768px) {
                margin-bottom: 32px;
            }
        }

        &:nth-child(1) {
            @include below(1440px) {
                margin-right: 80px;
            }
            @include below(768px) {
                margin-right: 0;
            }
        }

        &:nth-child(2) {
            @include below(1440px) {
                margin-right: 100px;
            }
        
            @include below(768px) {
                margin-right: 0;
            }
        }

        @include below(1500px) {
            width: calc(33.33333% - 20px);
            margin: 0 10px;  
        }

        @include below(1440px) {
            width: auto;
            margin: 0;
        }

        @include below(1024px) {
            // width: calc(50% - 20px);
        }

        @include below(575px) {
            width: 100%;
            margin: 30px 0 0;
        }

        &-title {
            font-size: 25px;
            font-weight: 600;
            line-height: 15px;
            margin-bottom: 50px;

            @include below(1440px) {
                font-size: 20px;
                line-height: 15px;
                margin-bottom: 25px;
            }

            @include below(768px) {
                font-size: 20px;
                line-height: 1.2;
                margin-bottom: 15px;
            }
        }
    }

    &__list {

        &-item {
            &:not(:last-child) {
                margin-bottom: 16px;
                @include below(1440px) {
                    margin-bottom: 8px;
                }
            }
        }

        &-link {
            font-size: 18px;
            font-weight: 600;
            line-height: 13px;
            color: $color_white;
            transition: text-decoration .3s linear;

            @include below(1440px) {
                font-size: 14px;
                line-height: 10px;
            }

            // @include below(768px) {
            //     font-size: 14px;
            //     line-height: 1.6;
            // }
            
            &:hover {
                text-decoration: underline;
            }
        }
    }

    &__bottom {
        display: flex;
        font-size: 18px;
        font-weight: 600;
        line-height: 31px;

        @include below(1024px) {
            flex-direction: column;
        }

        @include below(768px) {
            font-size: 14px;
            line-height: 1.6;
        }
    }

    &__copy {
        margin-right: 85px;
        @include below(1024px) {
            margin: 0 0 15px;
        }
        @include below(1440px) {
            font-size: 14px;
            line-height: 31px;
            margin-right: 60px;
        }
    }

    &__policy {
        color: $color_white;
        @include below(1440px) {
            font-size: 14px;
            line-height: 31px;
        }
    }
}
