@import "main-bx";

.travel {
    padding-bottom: 150px;

    @include below(1440px) {
        padding-bottom: 114px;
    }

    @include below(768px) {
        padding-bottom: 60px;
    }

    &__wrapper {
        position: relative;
    }

    &__img {
        
    }

    &__content {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        color: $color_white;

        @include below(1240px) {
            position: static;
            transform: translate(0, 0);
            color: $color_black;
            padding-top: 60px;
        }
    }

    &__title {
        @include title;
        text-align: center;
        margin-bottom: 32px;

        @include below(1440px) {
            margin-bottom: 26px;
        }

        @include below(768px) {
            margin-bottom: 16px;
        }
    }

    &__text {
        @include text;
        text-align: center;
        margin-bottom: 45px;

        @include below(1440px) {
            margin-bottom: 22px;
        }

        @include below(768px) {
            br {
                display: none;
            }
        }
    }

    &__form {
        width: 100%;
        max-width: 425px;
        margin: 0 auto;

        @include below(768px) {
            max-width: initial;
        }

        .form__btn {
            width: 100%;
            max-width: 305px;
            margin: 65px auto 0;

            @include below(1440px) {
                max-width: 240px;
                margin: 40px auto 0;
            }

            @include below(1240px) {
                border-color: $color_black;
                color: $color_black;
    
                &:hover:not(:active) {
                    color: $color_white;
                    background-color: $color_black;
                }
            }


            @include below(540px) {
                max-width: initial;
                margin: 40px 0 0;
            }
        }

        .form__input {
            border-color: $color_white;
            color: $color_white;

            &::placeholder {
                color: $color_white;
            }

            @include below(1240px) {
                border-color: $color_black;
                color: $color_black;

                &::placeholder {
                    color: $color_black;
                }
            }

            @include below(768px) {
                border-color: #EDEDED;
            }
        }

    }
}