@import "main-bx";

.filter {
    margin-bottom: 52px;

    @include below(768px) {
        margin-bottom: 32px;
    }
   
    &__flex {
        display: flex;

        @include below(768px) {
            flex-direction: column;
            align-items: start;
        }
    }

    &__item {
        display: flex;
        align-items: center;

        &:not(:last-child) {
            margin-right: 45px;


            @media (max-width: 1024px) {
                margin-right: 24px;
            }

            @include below(768px) {
               margin: 0 0 15px;
            }
        }

        @include below(540px) {
            flex-direction: column;
            align-items: start;
            width: 100%;
        }
    }

    &__text {
        font-weight: 600;
        font-size: 20px;
        line-height: 28px;
        color: $color_black;
        white-space: nowrap;
        margin-right: 25px;

        @media (max-width: 1440px) {
            font-size: 14px;
            line-height: 20px;
        }
        @media (max-width: 1024px) {
            margin-right: 12px;
        }

        @media (max-width: 540px) {
            margin-right: 0;
            margin-bottom: 12px;
        }
    }

    &__select {
        @include below(540px) {
            width: 100%;
        }
    }
}